<template>
  <div id="app" class="disable-select">
    <fa-icon
      class="menu-icon"
      icon="bars"
      v-bind:class="menu ? 'active-menu-icon' : ''"
      @click="menu = !menu"
    ></fa-icon>
    <div class="menu" v-show="menu">
      <h3 class="menu-title">Settings</h3>
      <div class="menu-content">
        <div class="menu-content-item">
          <h3>Countdown Options</h3>
          <div style="margin: 10px 0px">
            <h4>Title:</h4>
            <input type="text" v-model="title" />
          </div>
          <button
            :disabled="countdown || duration.asSeconds() < 1"
            @click="countdown = true"
          >
            Start
          </button>
          <button
            :disabled="!countdown || newCountdown.type === 'setdatetime'"
            @click="countdown = false"
          >
            Pause
          </button>
        </div>
        <div class="menu-content-item">
          <h3>New Countdown</h3>
          <h4>Countdown Type:</h4>
          <select v-model="newCountdown.type" style="margin-bottom: 10px">
            <option value="settime">Set hours etc.</option>
            <option value="setdatetime">Till Date and Time</option>
          </select>
          <div
            v-show="newCountdown.type === 'settime'"
            style="display: flex; flex-direction: column; align-items: center"
          >
            <div style="display: inline-flex">
              <h4>Days:</h4>
              <select v-model="newCountdown.days">
                <option value="0">0</option>
                <option v-for="i in 99" :key="i" :value="i">{{ i }}</option>
              </select>
            </div>
            <div style="display: inline-flex">
              <h4>H:</h4>
              <select v-model="newCountdown.hours">
                <option value="0">0</option>
                <option v-for="i in 24" :key="i" :value="i">{{ i }}</option>
              </select>
              <h4>M:</h4>
              <select v-model="newCountdown.minutes">
                <option value="0">0</option>
                <option v-for="i in 60" :key="i" :value="i">{{ i }}</option>
              </select>
              <h4>S:</h4>
              <select v-model="newCountdown.seconds">
                <option value="0">0</option>
                <option v-for="i in 60" :key="i" :value="i">{{ i }}</option>
              </select>
            </div>
          </div>
          <input
            v-show="newCountdown.type === 'setdatetime'"
            v-model="newCountdown.datetime"
            type="datetime-local"
          />
          <br />
          <button style="margin-top: 10px" @click="newCountdownFun">
            Create
          </button>
        </div>
        <div class="menu-content-item">
          <h3>Countdown Style</h3>
          <select v-model="style">
            <option
              v-for="s in styles"
              :key="s.name"
              :selected="s.selected"
              :value="s.name"
            >
              {{ s.name }}
            </option>
          </select>
        </div>
      </div>
    </div>
    <div class="content">
      <h2 class="heading">{{ title }}</h2>
      <div class="countdown">
        <div class="countdown-unit">
          <span>{{
            Math.floor(duration.asDays()).toString().split("").length === 1
              ? `0${Math.floor(duration.asDays())}`
              : Math.floor(duration.asDays())
          }}</span>
          <span class="countdown-unit-title"> DAYS </span>
        </div>
        <div class="countdown-colon">:</div>
        <div class="countdown-unit">
          <span>{{
            duration.hours().toString().split("").length === 1
              ? `0${duration.hours()}`
              : duration.hours()
          }}</span>
          <span class="countdown-unit-title"> HOURS </span>
        </div>
        <div class="countdown-colon">:</div>
        <div class="countdown-unit">
          <span>{{
            duration.minutes().toString().split("").length === 1
              ? `0${duration.minutes()}`
              : duration.minutes()
          }}</span>

          <span class="countdown-unit-title"> MINUTES </span>
        </div>
        <div class="countdown-colon">:</div>
        <div class="countdown-unit">
          <span>{{
            duration.seconds().toString().split("").length === 1
              ? `0${duration.seconds()}`
              : duration.seconds()
          }}</span>
          <span class="countdown-unit-title"> SECONDS </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from "moment";

export default {
  name: "App",
  data() {
    return {
      moment,
      menu: false,
      countdown: false,
      newCountdown: {
        type: "",
        days: "",
        hours: "",
        minutes: "",
        seconds: "",
        datetime: undefined,
      },
      title: "Countdown âŒš",
      duration: moment.duration(),
      style: "SimpleBlueBlack",
      styles: [{ name: "SimpleBlueBlack", selected: true }],
    };
  },
  created() {
    this.duration = moment.duration();
    setInterval(() => {
      if (this.countdown)
        this.duration = moment.duration(this.duration - 1000, "milliseconds");
    }, 1000);
  },
  methods: {
    newCountdownFun() {
      if (this.newCountdown.type === "settime") {
        this.duration = moment
          .duration(this.newCountdown.days || 0, "days")
          .add(this.newCountdown.hours || 0, "hours")
          .add(this.newCountdown.minutes || 0, "minutes")
          .add(this.newCountdown.seconds || 0, "seconds");
      } else if (this.newCountdown.type === "setdatetime") {
        let cd = moment(
          this.newCountdown.datetime,
          moment.HTML5_FMT.DATETIME_LOCAL
        ).unix();
        let time = cd - moment().unix();
        this.duration = moment.duration(time * 1000, "milliseconds");
        this.countdown = true;
        console.log(this.duration.humanize());
      }
    },
  },
};
</script>

